<template>
  <v-container>
    <v-form @submit.prevent class="d-flex justify-center">
      <v-col cols="12" sm="8">
        <v-row>
          <v-text-field
            v-model="username"
            label="Nazwa uytkownika"
            required
            autocomplete="username"
            :error-messages="usernameErrors"
            @input="$v.username.$touch()"
          >
            @blur="$v.username.$touch()" ></v-text-field
          >
        </v-row>
        <v-row>
          <v-text-field
            v-model="password"
            label="Haso"
            required
            :type="showPassword ? 'text' : 'password'"
            autocomplete="current-password"
            :error-messages="passwordErrors"
            @input="$v.password.$touch()"
            @blur="$v.password.$touch()"
            @click:append="showPassword = !showPassword"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            hint="Haso musi by dugoci od 8 do 30 znakw oraz posiada po jednej: duej literze, maej literze, cyfrze, znaku specjalnym: #$@!%\&*?."
          ></v-text-field>
        </v-row>
        <v-spacer></v-spacer>
        <v-row class="d-flex justify-end pt-4">
          <v-btn color="primary" @click="submit">Zaloguj</v-btn></v-row
        >
      </v-col>
    </v-form>
    <div class="text-center mt-5">
      <p><router-link to="#">Nie pamitam hasa.</router-link></p>
    </div>
  </v-container>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength, maxLength } from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],
  name: "Login",
  data: () => ({
    username: "",
    password: "",
    showPassword: false
  }),
  validations: {
    username: { required },
    password: {
      required,
      minLength: minLength(8),
      maxLength: maxLength(30),
      valid() {
        return /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[#$@!%&*?])[A-Za-z\d#$@!%&*?]{8,30}$/.test(
          this.$data.password
        );
      }
    }
  },
  computed: {
    usernameErrors() {
      const errors = [];
      if (!this.$v.username.$dirty) return errors;
      !this.$v.username.required &&
        errors.push("Nazwa uytkownika jest wymagana.");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push("Haso jest wymagane.");
      !this.$v.password.minLength &&
        errors.push("Haso musi skada si z conajmniej 8 znakw.");
      !this.$v.password.maxLength &&
        errors.push("Haso musi skada si z conajwyej 30 znakw.");
      !this.$v.password.valid &&
        errors.push(
          "Haso musi posiada po jednej: duej literze, maej literze, cyfrze, znaku specjalnym: #$@!%&*?."
        );
      return errors;
    }
  }
};
</script>
